.small-box{
   width: 310px;
   padding: 20px;
   margin-left: 5px;
 
   animation: fadeInOut 1s;
}
.small-box.bg-range{
background-color: #15236e!important;
width: 600px;
}

.small-box.bg-info {
    background-color: #15236e!important;
}
@keyframes fadeInOut {
  0% {
    opacity: 0;
    
  }
  70% {
    opacity: 1;
    transition: 450ms;
  }
 
}
h3{ 
    transform: scale(1.04) ;
   transition: 0.8s !important;
   transition-duration: 0.9s !important;
   transition-timing-function: ease all !important;
}
.small-box>p{
    text-align: right;
    color: rgb(138, 177, 255);
}
.small-box>.inner>h3{
    
    font-size: 30px;
    text-align: right;
    color: yellow;
}
.main-small-box{
    margin-left: 2%;
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 60px;
    border-bottom: 1px solid rgb(135, 135, 135);
    
}
.small-box .icon svg {
    font-size: 70px;
    position: absolute;
    right: 226px;
    top: 15px;
    transition: -webkit-transform .3s linear;
    transition: transform .3s linear;
    transition: transform .3s linear,-webkit-transform .3s linear;
}


.inner{
    color: black;
   margin-bottom: 6px ;
    border-bottom: 2px solid rgb(118, 116, 116);
}
.bg-info {
    background-color: #ffffff!important;
}
.container-heading{
    padding-top: 5px;
    margin-left: 5%;
    padding-bottom :30px;
}
.container-heading>h1{
   color: rgb(230, 251, 141);
    margin-top: 20px;
    padding-top: 40px;
    margin-bottom: -19px;
    font-size: 30px;
    
}
h2{  font-size: 15px !important;
    color: rgb(249, 226, 110) !important;
}
@media (max-width:1130px){
    .main-small-box{
        place-content: center;
     
    }
}

.css-hyum1k-MuiToolbar-root{
      background-color: #19072e;
}
.section{
    place-items: center;
}
@media (min-width: 600px){

.css-hyum1k-MuiToolbar-root {
    background-color: #19072e;
    min-height: 64px;
}}
