#icon-container {
  display: flex;
  justify-content: center;
  align-items: center;

}

.color-changing-icon {
  animation: colorAnimation 3s infinite;
}

@keyframes colorAnimation {
  0% {
    color: red;
  }
  50% {
    color: lightgreen;
  }
  100% {
    color: red;
  }
}




.css-1r9jet7 {
    background-color: #00064b !important;
    display: flex;
    -webkit-box-align: center;
    align-items: center;
    -webkit-box-pack: end;
    justify-content: flex-end;
    padding: 0px 8px;
    min-height: 56px;
}
.css-1ontqvh {
   background-color: #00064b !important;
    list-style: none;
    margin: 0px;
    padding: 8px 0px;
    position: relative;
}
.css-1l8j5k8 {
    background-color: #00064b !important;
    color: rgba(0, 0, 0, 0.87);
    transition: box-shadow 300ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    box-shadow: none;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    height: 100%;
    flex: 1 0 auto;
    z-index: 1200;
    position: fixed;
    top: 0px;
    outline: 0px;
    left: 0px;
    border-right: 1px solid rgba(0, 0, 0, 0.12);
}

.css-hlvchu .MuiDrawer-paper {
  background-color: #00064b !important;
    width: 240px;
    transition: width 225ms cubic-bezier(0.4, 0, 0.6, 1) 0ms;
    overflow-x: hidden;
}

