.img {
    width: 200px;
    border-radius: 5px;
    cursor: zoom-in;
    display: block;
  }
  
  .img-panel {
    padding: 5px;
    background-color: transparent;
    height: auto;
    width: auto;
    display: inline-block;
    transition: all 200ms ease;
    border-radius: 5px;
  }
  .img-panel:hover {
    background-color: #20A4F3;
  }
  
  .img-panel__selct {
    background-color: #20A4F3;
  }
  
  .display-box {
    padding: 1em;
    max-width: 1000px;
    min-width: 300px;
    margin: 1em auto;
    display: grid;
    grid-gap: 1em;
    grid-template-columns: repeat(auto-fill, 210px);
    grid-autoflow: row;
    justify-content: center;
  }
  
  .img-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: transparent;
    transition: all 200ms ease;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 99999999;
  }
  
  .img-wrapper__initial {
    background-color: rgba(0, 0, 0, 0.7);
  }
  
  .image__selected {
    max-width: 70vw;
    max-height: 80vh;
    display: block;
    cursor: pointer;
    transition: all 300ms ease-in-out;
    position: relative;
    border: transparent solid 4px;
    border-radius: 5px;
  }
  
  .img-selected-initial {
    border: white solid 4px;
  }
  
  .img-close {
    height: 50px;
    width: 50px;
    position: absolute;
    top: 1.5em;
    left: 1.5em;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 200ms ease-in-out;
    cursor: pointer;
    border-radius: 5px;
  }
  .img-close:hover {
    background-color: #CA3B4E;
  }
  .img-close:before {
    content: " ";
    height: 40px;
    width: 5px;
    background-color: white;
    transform: rotate(45deg);
    position: absolute;
    cursor: pointer;
  }
  .img-close:after {
    content: " ";
    height: 40px;
    width: 5px;
    transform: rotate(135deg);
    position: absolute;
    z-index: 100;
    background-color: white;
    cursor: pointer;
  }

  /* Hide the up and down arrows in input[type="number"] */
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0; /* Optional: Add additional styling as needed */
}

select::-webkit-inner-spin-button,
select::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0; /* Optional: Add additional styling as needed */
}
